
.game-card {
  width: 200px;
  height: 200px;
  border-radius: 5px;
  background-color: lightskyblue;
  margin: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 4px 8px 0 darkgrey ;
}

.game-img {
  max-width: 100%;
  max-height: 100%;
  padding: 15px;
}

.game-card:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0 4px 8px 0 grey;
}

@media (min-width: 600px) {
  .game-card {
    width: 180px;
    height: 180px;
  }
}


